      <v-menu
          bottom
          left
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
              icon
              color="blue"
              v-bind="attrs"
              v-on="on"
          >
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list dense shaped>
            <v-list-item
                class="px-3"
                v-for="(item, i) in drawList"
                :key="i"
                v-ripple
                @click="goItem(item)"
            >
              <v-list-item-icon>
                <v-icon v-text="item.icon"></v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title v-text="$t(item.name)"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>

        </v-list>
      </v-menu>
    </v-app-bar>

    <v-main>
      <v-container class="justify-center" style="max-width: 52rem; min-height: 52rem">
        <router-view/>
      </v-container>
    </v-main>

    <v-app-bar
        dense
        app
        color="white"
        bottom
    >
      <v-row no-gutters class="text-center">
<!--        <v-col class="px-0">-->
<!--        <v-btn-->
<!--            class="px-0"-->
