<script>
export default {
  name: 'App',
  data() {
    return {
      WhitePaperList: [
        {
          id: 0,
          name: 'en',
          icon: 'mdi-fire',
          link: '/en'
        },
        {
          id: 1,
          name: 'cn',
          icon: 'mdi-file-document-edit-outline',
          link: '/cn'
        },
      ],
      drawList: [
        {
          id: 0,
          name: 'draw.language',
          icon: 'mdi-google-translate',
          type: 'changeLang',
          link: 'changeLang'
        },
        {
          id: 1,
          name: 'draw.Docs',
          icon: 'mdi-file-document-edit-outline',
          link: '/about'
        },
        {
          id: 2,
          name: 'draw.Github',
          icon: 'mdi-codepen'
        },
        {
          id: 3,
          name: 'draw.WhitePaper',
          icon: 'mdi-book-open-variant',
          type: 'open',
          link: '/static/WhitePaper V1.0(En).pdf'
        },
        {
          id: 5,
          name: 'draw.BUGBounty',
          icon: 'mdi-information-outline',
          link: '/bug'
        },
        {
          id: 6,
          name: 'draw.Community',
          icon: 'mdi-forum-outline'
        },
        {
          id: 7,
          name: 'draw.Forum',
          icon: 'mdi-help-rhombus-outline',
          type: 'open',
          link: 'https://forum.opswap.io'
        },
        {
          id: 8,
          name: 'draw.IssueToken',
          icon: 'mdi-bitcoin',
          type: 'push',
          link: '/IssueToken'
        }
      ]
    }
  },
  filters: {
    ellipsis(value) {
      if (!value) return ''
      if (value.length > 20) {
        return value.slice(0, 8) + '...' + value.slice(-5)
      }
      return value
    },
    ellipsisLong(value) {
      if (!value) return ''
      if (value.length > 20) {
        return value.slice(0, 15) + '...' + value.slice(-15)
      }
      return value
    }
  },
  computed: {

    // 根据网络ID修改提示信息
    networkHint: function () {
      let hinText
      switch (this.$store.state.metamask.chainID) {
        case '1':
          hinText = 'Main network'
          break;
        case '2':
          hinText = 'Morden Test network'
          break;
        case '3':
          hinText = 'Ropsten Test Network'
          break;
        case '4':
          hinText = 'Rinkeby Test Network'
          break;
        case '5':
          hinText = 'Goerli Test Network'
          break;
        case '42':
          hinText = 'Kovan Test network'
          break;
        case this.$store.state.defaultNetwork:
          hinText = 'Arbitrum Layer2 v4'
          break;
        default:
          hinText = 'invalid network'
      }
      return hinText
    }
  },
  created() {
    console.log('app')
  },
  methods: {

    /**
     * 复制
     * 复制的内容可在任意地方粘贴
     */
    copyPaste(copyContext) {
      let input = document.createElement("input") // 直接构建input
      input.value = copyContext // 设置内容
      document.body.appendChild(input) // 添加临时实例
      input.select() // 选择实例内容
      document.execCommand("Copy") // 执行复制
      document.body.removeChild(input)  // 删除临时实例
    },

    // 切换语言
    changeLang() {
      console.log('lang', this.$i18n.locale)
      if (this.$i18n.locale === 'cn') {
        this.$i18n.locale = 'en'
        this.drawList[0].name = '中文'
      } else {
        this.$i18n.locale = 'cn'
        this.drawList[0].name = 'English'
      }
    },
    goItem(item) {
      console.log('go', item)
      if (item.link !== undefined && item.link !== null && item.link !== '') {
        switch (item.type) {
          case 'open': {
            window.open(item.link)
            break
          }
          case 'push': {
            this.$router.push(item.link)
            break
          }
          case 'changeLang': {
            this.changeLang()
            break
          }
        }
      }
    },
    // goHome() {
    //   this.$router.push('/')
    // },
    goSwap() {
      this.$router.push('/')
    },
    goAddLiquidity() {
      this.$router.push('/AddLiquidity')
    },
    goLoanContract() {
      this.$router.push('/LoanContract')
    },
    goMyAccount() {
      this.$router.push('/myAccount')
    },
    getReserves() {
      let _token0_address = this.pairData.token0.address
      let _token1_address = this.pairData.token1.address

      let that = this
      this.routerContract.methods.getReserves(_token0_address, _token1_address).call().then(function (response) {
        let token0_reserves = response[0].toString()
        let token1_reserves = response[1].toString()
        let token0_reserves_fix = window.web3.utils.fromWei(token0_reserves.toString())
        let token1_reserves_fix = window.web3.utils.fromWei(token1_reserves.toString())
        that.pairData.token0.reserves = Math.floor(token0_reserves_fix * 10000) / 10000
        that.pairData.token1.reserves = Math.floor(token1_reserves_fix * 10000) / 10000
        that.$forceUpdate() // 用于对象内数据更新到界面
        console.log('getReserves', token0_reserves, token1_reserves, token0_reserves_fix, token1_reserves_fix)
      })
    }
  }
}
</script>

